<template>
  <div class="container">
    <h1>База даних</h1>
    <hr>

    <div class="filters">
      <input v-model="filters.fullname" placeholder="Пошук за ПІБ" />
      <select v-model="filters.gender">
        <option value="">Стать (усі)</option>
        <option value="чоловіча">Чоловіча</option>
        <option value="жіноча">Жіноча</option>
      </select>
      <select v-model="filters.group">
        <option value="">Група (усі)</option>
        <option value="Малята">Малята</option>
        <option value="Середня">Середня</option>
        <option value="Старша">Старша</option>
      </select>
    </div>

    <p class="error" v-if="error">{{ error }}</p>

    <div class="posts-container">
      <ChildCard
        v-for="(post, index) in filteredPosts"
        :key="post._id"
        :child="post"
        @save="updated => savePost(post._id, updated, index)"
        @delete="deletePost(post._id, index)"
      />
    </div>
  </div>
</template>

<script>
import PostService from '../PostService';
import ChildCard from './ChildCard.vue';

export default {
  name: 'PostComponent',
  components: { ChildCard },
  data() {
    return {
      posts: [],
      error: '',
      filters: {
        fullname: '',
        gender: '',
        group: ''
      }
    };
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch (error) {
      this.error = error.message;
    }
  },
  methods: {
    async savePost(id, updatedPost, index) {
      try {
        const updated = await PostService.updatePost(id, updatedPost);
        this.posts.splice(index, 1, updated);
      } catch (err) {
        this.error = 'Помилка збереження: ' + err.message;
      }
    },
    async deletePost(id, index) {
      try {
        await PostService.deletePost(id);
        this.posts.splice(index, 1);
      } catch (err) {
        this.error = 'Помилка видалення: ' + err.message;
      }
    }
  },
  computed: {
    filteredPosts() {
      return this.posts.filter(post => {
        const nameMatch = post.fullname.toLowerCase().includes(this.filters.fullname.toLowerCase());
        const genderMatch = !this.filters.gender || post.gender === this.filters.gender;
        const groupMatch = !this.filters.group || post.group === this.filters.group;
        return nameMatch && genderMatch && groupMatch;
      });
    }
  }
};
</script>


