<!-- src/components/ChildCard.vue -->
<template>
  <div class="post">
    <div v-if="isEditing">
      <input v-model="localData.fullname" placeholder="ПІБ" /><br>
      <input v-model="localData.gender" placeholder="Стать" /><br>
      <input v-model="localData.momnumber" placeholder="Номер мами" /><br>
      <input v-model="localData.dadnumber" placeholder="Номер батька" /><br>
      <input v-model="localData.group" placeholder="Група" /><br>
      <button @click="onSave">Зберегти</button>
      <button @click="cancelEdit">Скасувати</button>
    </div>
    <div v-else>
      <p>ПІБ: {{ child.fullname }}</p>
      <p>Стать: {{ child.gender }}</p>
      <p>Номер мами: {{ child.momnumber }}</p>
      <p>Номер батька: {{ child.dadnumber }}</p>
      <p>Група: {{ child.group }}</p>
      <button @click="startEdit">Редагувати</button>
      <button class="delete" @click="onDelete">Видалити</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ChildCard',
  props: {
    child: Object,
  },
  data() {
    return {
      isEditing: false,
      localData: { ...this.child },
    };
  },
  emits: ['save', 'delete'],
  methods: {
    startEdit() {
      this.isEditing = true;
      this.localData = { ...this.child };
    },
    cancelEdit() {
      this.isEditing = false;
    },
    onSave() {
      this.$emit('save', this.localData);
      this.isEditing = false;
    },
    onDelete() {
      if (confirm('Ви впевнені, що хочете видалити цей запис?')) {
        this.$emit('delete');
      }
    },
  },
};
</script>
